'use client';

import React, { useState, useEffect } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientes: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const servicios = [
  { idServicio: 'SVC-001', nombre: 'Instalaci√≥n de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte t√©cnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

type Props = {
  open: boolean;
  venta?: VentaServicio | null;
  onClose: () => void;
  onGuardar: (ventaActualizada: VentaServicio) => void;
};

const VentaEditarModal = ({ open, venta, onClose, onGuardar }: Props) => {
  // Validaci√≥n defensiva: si no hay venta, no renderizar nada
  if (!open || !venta) return null;

  const [cliente, setCliente] = useState<Cliente>(venta.cliente);
  const [detalles, setDetalles] = useState<DetalleServicio[]>(venta.detalles);
  const [descuento, setDescuento] = useState(venta.descuento);
  const [error, setError] = useState('');

  useEffect(() => {
    if (venta) {
      setCliente(venta.cliente);
      setDetalles(venta.detalles);
      setDescuento(venta.descuento);
    }
  }, [venta]);

  const agregarServicio = (id: string) => {
    const servicio = servicios.find(s => s.idServicio === id);
    if (!servicio || detalles.some(d => d.idServicio === id)) return;
    const nuevoDetalle: DetalleServicio = {
      idDetalleServicio: `DET-${Date.now()}`,
      idServicio: servicio.idServicio,
      nombreServicio: servicio.nombre,
      costoTotal: servicio.costo,
    };
    setDetalles(prev => [...prev, nuevoDetalle]);
  };

  const eliminarDetalle = (id: string) => {
    setDetalles(prev => prev.filter(d => d.idServicio !== id));
  };

  const total = Math.max(0, detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento);

  const handleGuardar = () => {
    if (!cliente || detalles.length === 0) {
      setError('‚ö†Ô∏è Debes seleccionar cliente y al menos un servicio.');
      return;
    }

    const actualizada: VentaServicio = {
      ...venta,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: venta.fechaRegistro, // mantener la fecha original
    };

    onGuardar(actualizada);
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content max-w-xl">
        <button className="modal-close-btn" onClick={onClose}>‚úñ</button>
        <h2 className="text-xl font-bold text-indigo-700 mb-4">‚úèÔ∏è Editar Venta</h2>

        {/* Cliente */}
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">Cliente</label>
          <select
            className="border rounded w-full p-2"
            value={cliente.idCliente}
            onChange={(e) => {
              const seleccionado = clientes.find(c => c.idCliente === e.target.value);
              if (seleccionado) setCliente(seleccionado);
            }}
          >
            {clientes.map(c => (
              <option key={c.idCliente} value={c.idCliente}>
                {c.nombre}
              </option>
            ))}
          </select>
        </div>

        {/* Servicios */}
        <div className="mb-4">
          <p className="text-sm font-semibold mb-1">Servicios disponibles:</p>
          <div className="grid grid-cols-2 gap-3">
            {servicios.map(s => (
              <button
                key={s.idServicio}
                onClick={() => agregarServicio(s.idServicio)}
                className="border rounded-lg p-3 bg-white hover:bg-indigo-50 text-left transition"
              >
                <p className="font-medium text-indigo-800">{s.nombre}</p>
                <p className="text-sm text-gray-500">{formatCurrency(s.costo)}</p>
              </button>
            ))}
          </div>
        </div>

        {/* Servicios seleccionados */}
        <ul className="mb-4 space-y-2">
          {detalles.map((d) => (
            <li key={d.idDetalleServicio} className="flex justify-between items-center border p-3 rounded-md bg-gray-50">
              <span>{d.nombreServicio}</span>
              <button onClick={() => eliminarDetalle(d.idServicio)} className="text-sm text-red-600 hover:underline">
                Quitar
              </button>
            </li>
          ))}
        </ul>

        {/* Descuento y Total */}
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">Descuento (S/.)</label>
          <input
            type="number"
            value={descuento}
            min={0}
            onChange={(e) => setDescuento(Number(e.target.value))}
            className="border p-2 rounded w-full"
          />
        </div>

        <div className="text-right text-green-700 font-bold text-lg mb-4">
          Total: {formatCurrency(total)}
        </div>

        {error && <p className="text-sm text-red-600">{error}</p>}

        <div className="flex justify-end gap-2 mt-4">
          <button onClick={onClose} className="bg-gray-200 px-4 py-2 rounded-md">Cancelar</button>
          <button onClick={handleGuardar} className="bg-indigo-600 text-white px-4 py-2 rounded-md">Guardar cambios</button>
        </div>
      </div>
    </div>
  );
};

export default VentaEditarModal;
